html, body, #app{
    height: 100%;
    overflow: hidden;
}

.app-page {
    overflow: auto;
}
.copyright {
    padding: 5px 14px;
    text-align: center;
    font-size: 12px;
    color: #999;
}

.app-panel {
    background-color: white;
    margin: 9px 0;
    padding: 8px 14px;
    border-bottom: 1px solid #f2f2f2;
    color: #666;
    &-title {
        color: #333;
        font-size:16px;
        padding: 8px 0;
    }
    &-content {
        font-size: 14px;
        line-height: 20px;
        padding: 2px 0;
    }
    &-footer {
        font-size: 12px;
        color: #999;
        text-align: right;
    }
}


