
.xa-ripple-ink {
  display: block;
  overflow: hidden;
  border-radius: inherit;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  -webkit-mask-image: radial-gradient(circle, white, black);
}
.xa-ripple-ink .ripple {
  position: absolute;
  width: 0;
  height: 0;
  pointer-events: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  border-radius: 50%;
  background-color: currentColor;
  background-clip: padding-box;
  -webkit-transform: scale(0);
          transform: scale(0);
  transition: opacity 0.8s ease-out, -webkit-transform 0.8s ease-out;
  transition: transform 0.8s ease-out, opacity 0.8s ease-out;
  transition: transform 0.8s ease-out, opacity 0.8s ease-out, -webkit-transform 0.8s ease-out;
}
.xa-ripple-ink .ripple.held {
    -webkit-transform: scale(1);
            transform: scale(1);
}
.xa-ripple-ink .ripple.done {
    opacity: 0 !important;
}



