
.xa-arrow{color:#666;}
.xa-arrow.white:after{border-top-color:white;border-right-color:white;}
.xa-arrow:after{content:'';display:inline-block;width:8px;height:8px;margin-bottom:1px;margin-left:4px;border-top:1px solid #888;border-right:1px solid #888;transform:rotate(45deg);transition:0.3s transform;}
.xa-arrow.bottom:after{transform: rotate(135deg);margin-bottom:3px;}
.xa-arrow.top:after{transform: rotate(-45deg);margin-bottom:-1px;}


.xa-input{width:100%;border:0;outline:0;-webkit-appearance:none;background-color:transparent;font-size:inherit;color:inherit;height:24px;line-height:24px;}
.xa-input::-webkit-outer-spin-button,.xa-input::-webkit-inner-spin-button{-webkit-appearance:none;margin:0;}
.xa-input-label{width:80px;text-align:left;}
.xa-textarea{display:block;border:0;resize:none;width:100%;color:inherit;font-size:1em;line-height:inherit;outline:0;}


.form-title{height:36px;line-height:24px;position:relative;padding:7px 14px 5px;font-size:13px;color:#999;background-color:#f2f2f2;text-align:left;}
.form-title.required:before{content:'*';color:red;position:absolute;top:9px;left:5px;}
.form-cells{font-size:14px;color:#333;background-color:white;padding-left:14px;border-bottom:1px solid #e4e4e4;border-top:1px solid #e4e4e4;text-align:left;}
.form-title+.form-cells{border-top:1px solid #e4e4e4;}
.form-cell{line-height:24px;padding:10px 12px 10px 0;border-bottom:1px solid #f2f2f2;position:relative;}
.form-cell .required:before{content:'*';color:red;position:absolute;top:11px;left:-9px;}
.form-cells>.form-cell:last-child{border-bottom:0;}
.form-cell .form-help,.form-title .form-help{margin:0 14px 0 8px;color:red;font-size:14px;}
.form-cell.xa-arrow:after{transform:rotate(45deg) translate3d(6px, 6px, 0);}
.form-cell-bd{color:#666;word-wrap:break-word;word-break:break-all;}
.form-tip{color:#999;font-size:12px;margin-top:8px;line-height:20px;}
.form-tip .weui_icon_info:before{color:#ccc;font-size:15px;vertical-align:text-bottom;margin-right:3px;}

.xa-input{appearance: none;}

.xa-buttons{}
.xa-buttons.padding{padding:0 14px;}
.xa-button{position:relative;display:inline-block;padding:9px 14px;line-height:24px;font-size:14px;text-align:center;overflow:hidden;}
.xa-buttons.padding .xa-button + .xa-button{margin-left:10px;}
.xa-buttons:not(.padding) .xa-button.xa-bg-white+.xa-button.xa-bg-white:before{content:'';position:absolute;height:16px;width:1px;background:#ddd;top:13px;left:0;}


