@charset "utf-8";
@import "./variables";
@import "./reset";
@import "./common.css";
@import "./customer";

.xa {
  &-txt {
    @each $key, $val in $colors {
      &-#{$key} {color: $val;}
    }
    &-right {
      text-align: right;
    }
  }
  &-bg {
    @each $key, $val in $colors {
      &-#{$key} {background: $val;}
    }
  }
  &-flex {
    flex: 1;
  }
  &-webkit {
    &-box {
      display: flex;
    }
    &-vertical {
      display: flex;
      flex-direction: column;
    }
    &-center {
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }
  &-panel {
    background-color: white;
    padding: 10px 14px;
    margin-bottom: 10px;
  }
}



